export async function getLinkedPullRequests(context, { owner, repository, issue }) {
    if (!issue) {
        throw new Error("Issue is not defined");
    }
    const { data: timeline } = (await context.octokit.rest.issues.listEventsForTimeline({
        owner,
        repo: repository,
        issue_number: issue,
    }));
    const LINKED_PRS = timeline
        .filter((event) => event.event === "cross-referenced" && "source" in event && !!event.source.issue && "pull_request" in event.source.issue)
        .map((event) => event.source.issue);
    return LINKED_PRS.map((pr) => {
        return {
            organization: pr.repository?.full_name.split("/")[0],
            repository: pr.repository?.full_name.split("/")[1],
            number: pr.number,
            href: pr.html_url,
            author: pr.user?.login,
            state: pr.state,
            body: pr.body,
        };
    }).filter((pr) => pr !== null && pr.state === "open");
}
